
import {Link } from "react-router-dom";
import React from 'react';
import { Hexagon, HomeIcon, CompassIcon, PortfolioIcon, WorkspaceIcon, HamburgerMenu, PerksAndRewardsIcon, ExpirementIcon, SprintReviewIcon, ObjectivesIcon,
  YourPrototypesIcon, TeamBuildAndMastermindIcon, StartupToolkitIcon, AdvisementAndCoachingIcon, TractionIcon, CostAndRevenueIcon, CambioClubsAndEventsIcon
} from "./icons.js";
import "./navigation.css"
import { ProfileIcon } from "./icons.js";
import {logout, getTeamMembers} from "../backend"; 
import {
  isMobile
} from "react-device-detect";


class Badges extends React.Component {
    constructor(props) {
      super(props);
      window.addEventListener("resize", this.hideBadges)
      this.state = { show: (window.innerWidth > 750) };
    }
    hideBadges = () => {

      this.setState({ show: (window.innerWidth > 750) })
    }
    render() {
      if (this.state.show) {
        return (<div className="badge-div">
          <h1 className="badge-text">Badges</h1>
          <Hexagon color="#EECC7C" />
          <Hexagon color="#7CA9EE" />
          <Hexagon color="#8BEE7C" />
          <Hexagon color="#EE7C7C" />
          <Hexagon color="white" />
          <Link to="/login" >
            <button onClick={logout} style={{cursor: "pointer"}}>
          <div className="badge-text">Logout</div>
          </button></Link>
        </div>)
      }
      else {
        return null;
      }
    }
  }
