export class Navbar extends React.Component {
    render() {
      return (
        <div className="navbar-base">
          <div className="navbar-internal-div">

            <div onClick={this.props.sideNavAnimation} style={{ display: "inline-block" }}><HamburgerMenu /></div>
            <Link to="/"><HomeIcon /></Link>
            <h1 className="navbar-text">{this.props["pageName"]}</h1>

            <Badges />
            
          </div>
        </div>
      );
    }
  }

export class TeacherNavbar extends React.Component {
  render() {
    return (
      <div className="navbar-base">
          <div className="navbar-internal-div">

            <Link to="/teacher"><HomeIcon /></Link>
            <h1 className="navbar-text">Teacher</h1>            
          </div>
        </div>
    )
  }
}


export class SideNav extends React.Component {
  constructor(props) {
    super(props)
    this.state = {teamMembers: []}
    this.updateTeamMembers()
  }
  async updateTeamMembers() {
    var teamMembers = await getTeamMembers()
    this.setState({teamMembers: teamMembers})
  }
    render() {
      var sideNavIconSize = "3.5vh";
      return (
        <div className="sidenav" style={isMobile ? {width: "60vw", maxWidth: "60vw"} : null}>
          <div className="sidenav-internal-div">
            <Link to="/journey">
            <div className="sidenav-text">
            <CompassIcon size={sideNavIconSize} />
             <div style={{display: "inline-block", marginLeft: "10px"}}>Your Journey</div>
            </div>
            </Link>
            <Link to="/portfolio" >
            <div className="sidenav-text">
              <PortfolioIcon size={sideNavIconSize} />
               <div style={{display: "inline-block", marginLeft: "10px"}}>Your Portfolio</div>
            </div>
            </Link>
            <Link to="/workspace">
            <div className="sidenav-text">
                <WorkspaceIcon size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Your Workspace</div>
              </div></Link>
              <Link to="/perksandrewards">
              <div className="sidenav-text">
                <PerksAndRewardsIcon size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Perks and Rewards</div>
              </div></Link>
              {(//this.props.showTeams 
                this.props.showTeams? <><Link>
              <div className="sidenav-text">
                <ExpirementIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Experiment Lab</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <SprintReviewIcon size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Sprint Review</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <ObjectivesIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Your Objectives</div>
              </div></Link>
               </> : 
               <>
               <Link>
              <div className="sidenav-text">
                <ExpirementIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Experiment Lab</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <YourPrototypesIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Your Prototypes</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <TeamBuildAndMastermindIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>{"Team build & Mastermind"}</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <StartupToolkitIcon size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Startup Toolkit</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <AdvisementAndCoachingIcon size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>{"Advisement & Coaching"}</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <TractionIcon   size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>Traction</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <CostAndRevenueIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>{"Cost & Revenue"}</div>
              </div></Link>
              <Link>
              <div className="sidenav-text">
                <CambioClubsAndEventsIcon  size={sideNavIconSize} />
                 <div style={{display: "inline-block", marginLeft: "10px"}}>{"Cambio Clubs & Events"}</div>
              </div></Link>
              </>
               )}
               {this.props.showTeams ? <div style={{marginTop: "3vh"}}>
              <h1 style={{fontSize: "3vh"}}>Team</h1>
              <div style={{marginLeft: "15px"}}>
              {this.state.teamMembers.map((item, index) => <div style={{alignItems: "center", display: "flex", marginTop: "15px", fontSize: "2.3vh"}}><ProfileIcon size={"4.5vh"} 
              teamOrder={index}/> {item}</div>)}
              
              </div>
              </div>: null}
          </div>

        </div>
     )
    }
  }



  